     },
     onConfirm(time) {
         // eslint-disable-next-line no-console
      let timeTmp = new Date(time);
      this.market.next_follow_time = this.formateDate(timeTmp,"yyyy-MM-dd hh:mm:ss")
      this.showPicker = false;
    },
    formateDate(timeTmp,fmt){
       var o = {   
        "M+" : timeTmp.getMonth()+1,                 //月份   
        "d+" : timeTmp.getDate(),                    //日   
        "h+" : timeTmp.getHours(),                   //小时   
        "m+" : timeTmp.getMinutes(),                 //分   
        "s+" : timeTmp.getSeconds()                //秒
        };   
        if(/(y+)/.test(fmt))   
          fmt=fmt.replace(RegExp.$1, (timeTmp.getFullYear()+"").substr(4 - RegExp.$1.length));   
        for(var k in o)   
          if(new RegExp("("+ k +")").test(fmt))   
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));   
        return fmt; 
    },
    onSubmit(){
       let that = this;
       this.$http
          .post("/admin/business/save",this.market)
          .then(function (response) {
